<template>
    <div>
        <el-container>
            <el-header>
                上传雷达高度计数据
            </el-header>
            <el-main>
                <el-row :gutter="15">
                    <el-col :span="18" :class="{'col-17-step2':step===2}">
                        <el-card shadow="hover" v-show="step===1">
                            <el-form label-position="right" label-width="20%">
                                <el-form-item label="水库名称">
                                    <el-select v-model="form.selectedReservoir"
                                               placeholder="请选择">
                                        <el-option
                                                v-for="item in reservoirList"
                                                :label="item.name"
                                                :value="item.name"
                                                :key="item.id">
                                            {{item.name}}
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="卫星名称">
                                    <el-input v-model="form.satelliteName" placeholder="请输入卫星名称"
                                              style="width: 60%"></el-input>
                                </el-form-item>
                                <el-form-item label="卫星周期">
                                    <el-input v-model="form.cycle" placeholder="请输入周期" style="width: 60%"></el-input>
                                </el-form-item>
                                <el-form-item label="日期">
                                    <el-date-picker type="date" v-model="form.date"
                                                    placeholder="请输入日期"></el-date-picker>
                                </el-form-item>
                                <el-form-item label="左上角经纬度">
                                    <el-input v-model="form.topLeft" placeholder='以空格分隔经纬度，例如"111.01 32.97"'
                                              style="width: 60%"  @input="stepOneSetTest"></el-input>
                                </el-form-item>
                                <el-form-item label="右下角经纬度">
                                    <el-input v-model="form.lowerRight" placeholder='以空格分隔经纬度，例如"111.75 32.93"'
                                              style="width: 60%" @input="stepOneSetTest"></el-input>
                                </el-form-item>
                                <el-form-item label="选择文件">
                                    <el-upload
                                            class="upload-drag-list"
                                            drag
                                            action=""
                                            :auto-upload="false"
                                            :show-file-list="true"
                                            :on-change="handleFileChanges"
                                            :on-remove="handleFileChanges"
                                            :multiple="true"
                                            :file-list="fileList">
                                        <i class="el-icon-upload"></i>
                                        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>

                                    </el-upload>
                                </el-form-item>
                                <el-button @click="stepOneClickNext"
                                           :disabled="stepOneClickNextDisable"
                                           v-text="showUploadPercentage">
                                </el-button>
                            </el-form>
                        </el-card>
                        <el-card shadow="hover"
                                 class="el-card-step2"
                                 v-show="step===2"
                                 v-loading="translating"
                                 :body-style="{
                                     flex:'1',
                                     display:'flex',
                                     flexDirection:'column',
                                     padding:'0',
                                 }">
                            <baidu-map
                                    class="radar-map"
                                    :scroll-wheel-zoom="true"
                                    :center="centerPoint"
                                    :zoom="zoom"
                                    :continuous-zoom="true"
                                    @moving="stepTwoUpdateMapZoom"
                                    @moveend="stepTwoUpdateMapZoom"
                                    @zoomend="stepTwoUpdateMapZoom"
                            >
                                <bm-marker
                                        v-for="item in resultList"
                                        :position="{lng: item.translatedLng, lat: item.translatedLat}"
                                        :icon="pointer"
                                        :key="item.index"
                                        :offset="{height: 0, width: 15}"
                                        @click="stepTwoHandleMarkerClick(item.index)"
                                >
                                    <bm-label
                                            :content="item.index + 1 + ''"
                                            :offset="{width: 15, height: -10}"
                                    >
                                    </bm-label>
                                </bm-marker>
                            </baidu-map>
                            <div :style="{
                                display: 'flex',
                                alignItems: 'center',
                                margin:'10px',
                            }">
                                <el-select v-model="selectRadar.index"
                                           style="flex-basis: 100px; margin-right: 15px"
                                           @change="stepTwoHandleSelectChange">
                                    <el-option v-for="item in resultList"
                                               :key="item.index"
                                               :value="item.index"
                                               :label="item.index+1"
                                    >
                                        {{item.index+1}}
                                    </el-option>
                                </el-select>
                                <div style="flex:1" v-show="selectRadar.index!==''">
                                    经度：<span class="el-card-step2-span" v-text="selectRadar.lng"></span>
                                </div>
                                <div style="flex:1" v-show="selectRadar.index!==''">
                                    纬度：<span class="el-card-step2-span" v-text="selectRadar.lat"></span>
                                </div>
                                <div style="flex:1" v-show="selectRadar.index!==''">
                                    水位：<span class="el-card-step2-span" v-text="selectRadar.level"></span>
                                </div>
                                <div style="flex:2">
                                    <el-button style="margin-bottom: 0" type="success" @click="stepTwoClickSave">
                                        确认并保存
                                    </el-button>
                                    <el-button style="margin-bottom: 0" @click="stepTwoClickBack">
                                        上一步
                                    </el-button>
                                </div>

                            </div>

                        </el-card>
                        <el-card v-show="step===3">
                            <i class="el-icon-success"></i>
                            数据已保存
                            <el-button @click="stepThreeClickWatchData">
                                查看数据
                            </el-button>
                        </el-card>
                    </el-col>

                    <el-col :span="6">
                        <el-card shadow="hover">
                            <el-steps direction="vertical" :active="step-1">
                                <el-step title="上传文件" description="输入信息，上传雷达高度计文件"></el-step>
                                <el-step title="选择并确认数据" description="选择合适的水位数据"></el-step>
                                <el-step title="上传完成"></el-step>
                            </el-steps>
                        </el-card>
                    </el-col>


                </el-row>


            </el-main>
        </el-container>
    </div>
</template>

<script>
    export default {
        name: "uploadRadarLevel",
        data: function () {
            return {
                fileList: [],
                reservoirList: [],
                form: {
                    selectedReservoir: '',
                    satelliteName: '',
                    cycle: '',
                    date: '',
                    topLeft: '',
                    lowerRight: '',

                },
                selectRadar: {
                    index: '',
                    lng: '',
                    lat: '',
                    level: '',
                },
                uploadPercentage: 0,
                uploading: false,
                translating: false,
                resultList:[],
                step: 1,
                pointer: {
                    url: '/marker.png',
                    size: {width: 50, height: 50},
                },
                centerPoint:{
                    lng:'',
                    lat:'',
                },
                zoom: 15,
            }
        },
        computed: {
            stepOneClickNextDisable() {
                return this.form.selectedReservoir === ''
                    || this.form.satelliteName === ''
                    || this.form.cycle === ''
                    || this.form.date === ''
                    || this.form.topLeft === ''
                    || this.form.lowerRight === ''
                    || this.fileList.length !== 4
                    || this.uploading;
            },
            showUploadPercentage() {
                if (this.uploading) {
                    if (this.uploadPercentage === 100) {
                        return "正在处理数据"
                    } else {
                        return "正在上传：" + this.uploadPercentage + "%"
                    }
                } else {
                    return "下一步"
                }
            }
        },
        methods: {
            handleFileChanges: function (file, fileList) {
                this.fileList = fileList;
            },
            stepOneClickNext: function () {
                //check
                this.uploading = true;
                let formData = new FormData();
                formData.append('reservoirName', this.form.selectedReservoir);
                formData.append('satelliteName', this.form.satelliteName);
                formData.append('cycle', this.form.cycle);
                formData.append('date', this.form.date);
                formData.append('topLeft', this.form.topLeft);
                formData.append('lowerRight', this.form.lowerRight);
                for (let i = 0; i < this.fileList.length; i++) {
                    formData.append('radarFile', this.fileList[i].raw)
                }
                this.axios({
                    url: "/upload/radar",
                    method: "post",
                    data: formData,
                    headers: {
                        "Content-Type": "multipart/form-data;charset=utf-8"
                    },
                    onUploadProgress: progressEvent => {
                        this.uploadPercentage = (progressEvent.loaded / progressEvent.total * 100 | 0);
                    }
                }).then((response) => {
                    if (response.status === 200) {
                        this.resultList = response.data;
                        let self = this;
                        self.step++;
                        this.translating = true;
                        // eslint-disable-next-line no-undef
                        doPointTranslate(this.resultList, function (data) {
                            for (let i = 0; i<data.length;i++){
                                self.resultList[i].translatedLng = data[i].lng;
                                self.resultList[i].translatedLat = data[i].lat;
                            }
                            self.getCenterPoint();
                            self.uploading = false;
                            self.translating = false;
                        });
                    }
                })
            },
            stepOneSetTest: function(){
                if (this.form.topLeft === '//' || this.form.lowerRight === '//'){
                    this.form.topLeft = '113.43 32.97';
                    this.form.lowerRight = '114.12 32.93';
                }
            },
            getCenterPoint: function(){
                let [c, lng, lat] = [0, 0, 0];
                for (let i = 0; i<this.resultList.length; i++){
                    c++;
                    lng += this.resultList[i].translatedLng;
                    lat += this.resultList[i].translatedLat;
                }
                this.centerPoint.lng = lng / c;
                this.centerPoint.lat = lat / c;
            },
            stepTwoClickBack: function () {
                this.step--;
            },
            stepTwoHandleSelectChange: function () {
                const t = this.resultList[this.selectRadar.index];
                this.selectRadar.lng = t.lng;
                this.selectRadar.lat = t.lat;
                this.centerPoint.lng = t.translatedLng;
                this.centerPoint.lat = t.translatedLat;
                this.selectRadar.level = t.level.toFixed(3);
            },
            stepTwoHandleMarkerClick: function(e){
                this.selectRadar.index = e;
                this.stepTwoHandleSelectChange();
            },
            stepTwoUpdateMapZoom(e) {
                this.zoom = e.target.getZoom()
            },
            stepTwoClickSave: function () {
                this.axios.get("/upload/radar/choose?index=" + this.selectRadar.index).then((response) => {
                    if (response.data === "success") {
                        this.step++;

                    } else {
                        console.log("error")
                    }
                })
            },
            stepThreeClickWatchData: function () {
                this.$router.push('/dm')

            },

        },
        mounted() {
            this.axios.get('/api/reservoirInfo/reservoir').then((response) => {
                if (response.status === 200) {
                    this.reservoirList = response.data;

                } else {
                    console.log("error")
                }
            });
            let input = document.getElementsByClassName("el-upload__input");
            input[0].setAttribute('webkitdirectory','true');
        }
    }
</script>

<style scoped>
    .el-container {
        min-height: 100%;
    }

    .el-main {
        height: 100%;
        display: inline-flex;
        padding: 0 5px 0 20px;
    }

    .el-row {
        width: 100%;
        overflow: hidden;
    }

    .el-col-18 {
        height: 100%;
        overflow-y: auto;
    }

    .el-col-18::-webkit-scrollbar {
        display: none;
    }

    .col-17-step2 {
        display: flex;
    }

    .el-progress {
        float: right;
        width: 50%;
        margin-top: 10px;
    }

    .el-button {
        float: right;
        margin-bottom: 20px;
        margin-left: 20px;
    }

    .el-table {
        margin-top: 10px;
        margin-bottom: 20px;
    }

    .el-card {
        border: 1px solid #d4d4d4;
        margin-top: 20px;
        margin-bottom: 20px;
    }

    .el-card-step2 {
        flex: 1;
        display: flex;
    }

    .el-steps {
        height: 200px;
    }

    .radar-map {
        flex: 1;
    }

    .el-card-step2-span {
        color: #00b776;
    }

    .upload-drag-list {
        max-width: 70%;
    }
</style>
